import { useCallback, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import { useTourEventState } from '../global/TourStats';
import {
  selectDataMap,
  selectConfigMap,
  selectSceneSelected,
} from '../selectors';

// interface Props {}

export const useEventMap = () => {
  const mapData = useSelector(selectDataMap);
  const configMap = useSelector(selectConfigMap);
  const sceneSelected = useSelector(selectSceneSelected);

  const [, setTourEventStats] = useTourEventState('state');

  const markerSelected = useMemo(
    () =>
      mapData?.markers?.filter(
        item => item.target_scene_id === sceneSelected?.id,
      )[0] || undefined,
    [mapData, sceneSelected],
  );

  const configDisplayPanel = useMemo(
    () => (configMap?.map?.type === 'panel' ? configMap?.map : undefined),
    [configMap?.map],
  );

  const configDisplayBox = useMemo(
    () =>
      configMap?.map?.type === 'box'
        ? {
            ...{
              offsetBottom: 48,
              offsetLeft: 10,
              offsetRight: 10,
              offsetTop: 10,
            },
            ...configMap?.map,
          }
        : undefined,
    [configMap?.map],
  );

  const [visiblePopup, setVisiblePopup] = useState(false);

  const onCloseMap = useCallback(() => {
    setTourEventStats(prev => ({ ...prev, isVisibleMap: false }));
  }, [setTourEventStats]);

  const onOpenPopup = useCallback(() => setVisiblePopup(true), []);
  const onClosePopup = useCallback(() => setVisiblePopup(false), []);

  return useMemo(
    () => ({
      visiblePopup,
      sceneSelected,
      markerSelected,
      configMap,
      configDisplayBox,
      mapData,
      configDisplayPanel,
      onCloseMap,
      onClosePopup,
      onOpenPopup,
    }),
    [
      visiblePopup,
      sceneSelected,
      markerSelected,
      configMap,
      configDisplayBox,
      mapData,
      configDisplayPanel,
      onCloseMap,
      onClosePopup,
      onOpenPopup,
    ],
  );
};
