/* eslint-disable jsx-a11y/mouse-events-have-key-events */
import {
  forwardRef,
  memo,
  Ref,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useSelector } from 'react-redux';
import dynamic from 'next/dynamic';
import { DEFAULT_VALUE_MAP_CONFIG } from '@containers/Home/constants';
import GoogleMapReact from 'google-map-react';
import { selectListScene } from '../../selectors';
import { GoogleMapWrapper } from './styled';
import { I_MapConfig, I_Map, I_Scene } from '../../types';

const Marker = dynamic(() => import('./Marker'));

export type Type_RefFuncGoogleMap = {
  onFitBounds: () => void;
  onPanToMarker: (marker: I_Map['markers'][0]) => void;
};

interface Props {
  mapData?: I_Map;
  sceneSelected?: I_Scene;
  configMap?: I_MapConfig;
  markerSelected?: I_Map['markers'][0];
  onChangeScene: (scene: I_Scene) => void;
}

const Map = forwardRef(
  (
    { configMap, sceneSelected, mapData, markerSelected, onChangeScene }: Props,
    ref: Ref<Type_RefFuncGoogleMap>,
  ) => {
    const mapRef = useRef<any>(null!);
    const [isMapLoaded, setIsMapLoaded] = useState(false);

    const listScene = useSelector(selectListScene);

    const configRadar = useMemo(
      () => ({
        ...DEFAULT_VALUE_MAP_CONFIG.radar,
        ...configMap?.radar,
      }),
      [configMap?.radar],
    );
    const configMarker = useMemo(
      () => ({
        ...DEFAULT_VALUE_MAP_CONFIG.marker,
        ...configMap?.marker,
      }),
      [configMap?.marker],
    );

    const customMapStyles = useMemo(() => {
      try {
        return configMap?.mapStyle
          ? JSON.parse(configMap?.mapStyle || '[]')
          : [];
      } catch {
        return [];
      }
    }, [configMap]);

    const onFitBounds = useCallback(() => {
      const _window = window as any;
      if (_window.google) {
        const bounds = new _window.google.maps.LatLngBounds();
        mapData.markers?.forEach(marker => {
          bounds.extend({ lat: marker.lat, lng: marker.lng });
        });
        mapRef.current.fitBounds(bounds);
      }
    }, [mapData]);

    const onPanToMarker = useCallback((marker: typeof markerSelected) => {
      mapRef.current?.panTo({
        lat: marker?.lat,
        lng: marker?.lng,
      });
    }, []);

    const onClickMarker = useCallback(
      (_key, childProps) => {
        if (childProps?.marker?.target_scene_id) {
          const scene = listScene.filter(
            item => item.id === childProps?.marker?.target_scene_id,
          )[0];
          if (scene) onChangeScene(scene);
        }
      },
      [listScene, onChangeScene],
    );

    useEffect(() => {
      /**
       * NOTE: default view OR fit bounds
       */
      if (isMapLoaded) {
        if (markerSelected) onPanToMarker(markerSelected);
        else onFitBounds();
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isMapLoaded, markerSelected]);

    useImperativeHandle(ref, () => ({
      onFitBounds: () => onFitBounds(),
      onPanToMarker: (marker: typeof markerSelected) => onPanToMarker(marker),
    }));

    return (
      <GoogleMapWrapper>
        <GoogleMapReact
          bootstrapURLKeys={{
            key:
              process.env.NEXT_PUBLIC_GOOGLE_KEY ||
              'AIzaSyAcwcJSrJ4_PqEIJoQCtC5RP988Eka0YE8',
          }}
          draggable
          defaultCenter={{
            lat: 0,
            lng: 0,
          }}
          defaultZoom={12}
          options={() => ({
            minZoom: 2,
            panControl: false,
            mapTypeControl: false,
            scrollwheel: true,
            streetViewControl: false,
            mapTypeId: configMap?.mapType,
            disableDefaultUI: true,
            styles: customMapStyles,
          })}
          scrollWheelZoom="center"
          yesIWantToUseGoogleMapApiInternals
          onGoogleApiLoaded={({ map }) => {
            mapRef.current = map;
            setIsMapLoaded(true);
          }}
          onChildClick={onClickMarker}
        >
          {mapData?.markers.map(item => (
            <Marker
              key={item.id}
              lat={item.lat}
              lng={item.lng}
              marker={item}
              configRadar={configRadar}
              configMarker={configMarker}
              northRadar={sceneSelected?.north_ath}
              isTurnOnRadar={configRadar.isEnabled}
              isActive={markerSelected?.id === item.id}
            />
          ))}
        </GoogleMapReact>
      </GoogleMapWrapper>
    );
  },
);

export default memo(Map);
